import { defineComponent } from "vue";
import EventEmitter from "@/util/EventEmitter";
import { mapActions } from "vuex";
import LanguageSelector from "@/components/LanguageSelector/index.vue";

export default defineComponent({
  name: "NavbarComponent",
  components: {
    LanguageSelector,
  },
  data() {
    return {
      showSidebar: false,
      context: process.env.VUE_APP_CONTEXT,
      appVersion: require("../../../lib/version.js"),
    };
  },
  mounted() {
    EventEmitter.emitter.on("sidebar-toggle-sidebar", (value: boolean) => {
      this.showSidebar = value;
    });
  },
  methods: {
    ...mapActions("editor", ["removeTemplateLocalStorage"]),
    ...mapActions("auth", ["removeLocalStorageAuthToken"]),
    logout() {
      this.$auth.logout({
        returnTo: `${window.location.origin}${this.context}`,
      });
      this.removeTemplateLocalStorage();
      this.removeLocalStorageAuthToken();
    },
    toggleMenu() {
      this.showSidebar = !this.showSidebar;
      EventEmitter.emitter.emit("sidebar-toggle-navbar", this.showSidebar);
    },
  },
});
