import "bootstrap/dist/js/bootstrap.min.js";
import SidebarComponent from "@/components/Sidebar/index.vue";
import NavbarComponent from "@/components/Navbar/index.vue";
import EventEmitter from "@/util/EventEmitter";
import { defineComponent } from "vue";
import { v4 as uuidv4 } from "uuid";

export default defineComponent({
  components: {
    SidebarComponent,
    NavbarComponent,
  },
  data() {
    return {
      uid: uuidv4(),
      online: navigator.onLine,
    };
  },
  mounted() {
    EventEmitter.emitter.on("sidebar-toggle-navbar", function (value: boolean) {
      EventEmitter.emitter.emit("sidebar-toggle-sidebar", value);
    });
    EventEmitter.emitter.on("navigator-online", (status: boolean) => {
      this.online = status;
    });
  },
});
